header {
  min-height: 100vh;
  background: transparent radial-gradient(closest-side at 50% 48%, #2C3138 0%, #171A1F 100%) 0% 0% no-repeat padding-box;
  position: relative;
  padding-bottom: 15rem;
}
header .header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
@media all and (min-width: 320px) and (max-width: 1200px) {
  header .header {
    padding: 0 2rem 0;
  }
}
header .header .header__logo .header__logo-name {
  font-family: "Rajdhani", sans-serif;
  font-weight: 700;
  font-size: 1.8rem;
  color: #fff;
}
header .header .header__login {
  margin-top: 1.1rem;
}
header .header .header__login .header__login-btn {
  font-family: "Rajdhani", sans-serif;
  font-weight: 500;
  font-size: 1.4rem;
  color: #F9F9F9;
  background-color: transparent;
  border-radius: 0.5rem;
  border: 0.1rem solid #737373;
  width: 7.5rem;
  height: 4.8rem;
  cursor: pointer;
}
header .header .header__login .header__login-btn:hover {
  border-color: white;
}

.mining {
  background: url("/images/Mining text@2x.png");
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: contain;
  max-width: 151.7rem;
  height: 63.8rem;
  position: relative;
  display: grid;
  align-items: flex-end;
}
@media all and (min-width: 320px) and (max-width: 1000px) {
  .mining {
    background-position: center center;
  }
}
.mining::after {
  content: url("/images/astro.png");
  position: absolute;
  top: 17%;
  right: 8%;
  display: block;
}
@media all and (min-width: 320px) and (max-width: 1000px) {
  .mining::after {
    top: 0;
  }
}
.mining .mining_list {
  display: flex;
  grid-gap: 5rem;
  flex-wrap: wrap;
}
@media all and (min-width: 320px) and (max-width: 1200px) {
  .mining .mining_list {
    grid-gap: 2rem;
  }
}
.mining .mining_list .mining_list-item {
  list-style: none;
  position: relative;
}
.mining .mining_list .mining_list-item .mining__wrapper {
  display: flex;
  grid-gap: 2rem;
}
.mining .mining_list .mining_list-item .mining__wrapper .e {
  fill: #707070;
}
.mining .mining_list .mining_list-item .mining__wrapper:hover .mining_list-link {
  color: #F9F9F9;
}
.mining .mining_list .mining_list-item .mining__wrapper:hover .e, .mining .mining_list .mining_list-item .mining__wrapper:hover .f {
  fill: #F9F9F9;
}
.mining .mining_list .mining_list-link {
  font-family: "Rajdhani", sans-serif;
  font-weight: 600;
  font-size: 3.2rem;
  color: #737373;
  text-decoration: none;
}

.mining_question {
  font-family: "Rajdhani", sans-serif;
  font-weight: 600;
  font-size: 2rem;
  color: #737373;
  text-align: center;
  margin-top: 8.5rem;
  position: relative;
}
.mining_question:hover {
  color: #F9F9F9;
}

.mining_arrow {
  position: absolute;
  bottom: 17%;
  right: 50%;
  display: block;
  cursor: pointer;
  transition: 0.3s bottom ease-in;
}
@media all and (min-width: 320px) and (max-width: 1100px) {
  .mining_arrow {
    bottom: 10%;
  }
}
.mining_arrow:hover {
  bottom: 16%;
}
@media all and (min-width: 320px) and (max-width: 1100px) {
  .mining_arrow:hover {
    bottom: 9%;
  }
}
.mining_arrow:hover .a {
  stroke: #518EF8;
}

.modal {
  width: 42rem;
  height: 32.4rem;
  background: #F9F9F9 0% 0% no-repeat padding-box;
  box-shadow: 0 0 1rem #171A1F80;
  border-radius: 1rem;
  max-width: 100%;
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 2;
  display: none;
}
@media all and (min-width: 320px) and (max-width: 580px) {
  .modal {
    width: 29rem;
  }
}
.modal .modal__title {
  text-align: center;
  font-family: "Rajdhani", sans-serif;
  font-weight: 600;
  font-size: 3.2rem;
  color: #F9F9F9;
  margin-top: -5.5rem;
}
.modal .modal_content {
  height: 100%;
}

.success {
  display: flex;
  justify-content: center;
  align-items: center;
  color: green;
  font-size: 6.5rem;
}
@media all and (min-width: 320px) and (max-width: 580px) {
  .success {
    font-size: 5rem;
  }
}
.success h4 {
  margin-right: 5%;
}

.invalid {
  border: 0.1rem solid #F14336 !important;
  outline: none;
  box-shadow: 0 0 0.2rem #F14336;
}

.valid {
  border: 0.1rem solid #28B446 !important;
  outline: none;
  box-shadow: 0 0 0.2rem #28B446;
}

.form-message {
  text-align: center;
  font-family: "Rajdhani", sans-serif;
  font-weight: 600;
  font-size: 3.2rem;
  color: #F9F9F9;
}
.form-message .form-error-list {
  margin-top: -3rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

form {
  height: 100%;
}
form .login__wrapper {
  display: flex;
  grid-template-columns: 1fr;
  flex-direction: column;
  align-items: center;
  height: 100%;
  justify-content: center;
}
form .login__wrapper input {
  width: 38rem;
  height: 4.2rem;
  background-color: #FFFFFF;
  border: 0.1rem solid #C1C1C1;
  border-radius: 0.3rem;
  text-indent: 2rem;
  max-width: 100%;
}
form .login__wrapper input:focus::-webkit-input-placeholder {
  opacity: 0;
}
form .login__wrapper input:focus::-moz-placeholder {
  opacity: 0;
}
form .login__wrapper input:focus:-ms-input-placeholder {
  opacity: 0;
}
form .login__wrapper input:focus::-ms-input-placeholder {
  opacity: 0;
}
form .login__wrapper input:focus::placeholder {
  opacity: 0;
}
@media all and (min-width: 320px) and (max-width: 580px) {
  form .login__wrapper input {
    width: 25rem;
  }
}
form .login__wrapper input:first-of-type {
  margin-bottom: 1rem;
  margin-top: 2rem;
}
form .login__wrapper ::-webkit-input-placeholder {
  font-family: "Rajdhani", sans-serif;
  font-weight: 500;
  font-size: 1.8rem;
  color: #737373;
  text-align: center;
  text-align: left;
  padding-left: 0.5rem;
  opacity: 1;
  -webkit-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
}
form .login__wrapper ::-moz-placeholder {
  font-family: "Rajdhani", sans-serif;
  font-weight: 500;
  font-size: 1.8rem;
  color: #737373;
  text-align: center;
  text-align: left;
  padding-left: 0.5rem;
  opacity: 1;
  -moz-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
}
form .login__wrapper :-ms-input-placeholder {
  font-family: "Rajdhani", sans-serif;
  font-weight: 500;
  font-size: 1.8rem;
  color: #737373;
  text-align: center;
  text-align: left;
  padding-left: 0.5rem;
  opacity: 1;
  -ms-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
}
form .login__wrapper ::-ms-input-placeholder {
  font-family: "Rajdhani", sans-serif;
  font-weight: 500;
  font-size: 1.8rem;
  color: #737373;
  text-align: center;
  text-align: left;
  padding-left: 0.5rem;
  opacity: 1;
  -ms-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
}
form .login__wrapper ::placeholder {
  font-family: "Rajdhani", sans-serif;
  font-weight: 500;
  font-size: 1.8rem;
  color: #737373;
  text-align: center;
  text-align: left;
  padding-left: 0.5rem;
  opacity: 1;
  transition: opacity 0.3s ease-out;
}
form .login__wrapper .login__btn {
  font-family: "Rajdhani", sans-serif;
  font-weight: 600;
  font-size: 1.8rem;
  color: #fff;
  width: 38rem;
  height: 4.8rem;
  background-color: #2AD46F;
  box-shadow: 0.5rem 0.5rem 0.1rem #E2FFEE;
  border-radius: 0.3rem;
  text-align: center;
  border: none;
  outline: none;
  margin-top: 2rem;
  cursor: pointer;
}
form .login__wrapper .login__btn:hover {
  background-color: #28B446;
  opacity: 0.9;
}
@media all and (min-width: 320px) and (max-width: 580px) {
  form .login__wrapper .login__btn {
    width: 25rem;
  }
}
form .login__wrapper .login_paragraph {
  font-family: "Rajdhani", sans-serif;
  font-weight: 500;
  font-size: 1.6rem;
  color: #7A7A7B;
  text-align: center;
  width: 38.1rem;
  height: 2.1rem;
}
form .login__wrapper .login_paragraph:last-of-type {
  margin-top: 0rem;
  padding-bottom: 1rem;
}
form .login__wrapper .login_paragraph .login_paragraph-link {
  font-family: "Rajdhani", sans-serif;
  font-weight: 600;
  font-size: 1.6rem;
  color: #2C3138;
  text-decoration: none;
}
form .login__wrapper .login_paragraph .login_paragraph-link:hover {
  text-decoration: underline;
}

