@media all and (min-width: 320px) and (max-width: 580px) {
  .select2-container {
    width: 25rem !important;
  }
}

.coin__wrapper {
  grid-gap: 1rem;
  display: grid;
  grid-template-columns: 20px 70px 50px 60px;
  align-items: center;
  justify-content: center;
}
@media all and (min-width: 320px) and (max-width: 580px) {
  .coin__wrapper {
    grid-gap: 0.5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.coin__wrapper .coin-one {
  font-family: "Rajdhani", sans-serif;
  font-weight: 600;
  font-size: 1.4rem;
  color: #181B1F;
}
.coin__wrapper .coin-two, .coin__wrapper .coin-three {
  font-family: "Rajdhani", sans-serif;
  font-weight: 500;
  font-size: 1.4rem;
  color: #181B1F;
}
.coin__wrapper .coin-four, .coin__wrapper .coin-five {
  font-family: "Rajdhani", sans-serif;
  font-weight: 500;
  font-size: 1.4rem;
  color: #737373;
}
.coin__wrapper .coin__text-one {
  grid-column-start: 4;
  grid-column-end: 5;
  margin-left: 1rem;
}
@media all and (min-width: 320px) and (max-width: 580px) {
  .coin__wrapper .coin__text-one {
    margin-left: unset;
  }
}
.coin__wrapper .coin__text-two {
  grid-column-start: 5;
  grid-column-end: 5;
}

.coin__item {
  display: contents;
}

.coin__text {
  font-family: "Rajdhani", sans-serif;
  font-weight: 600;
  font-size: 1.4rem;
  color: #181B1F;
  display: flex;
}

.select2-hidden-accessible {
